html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    -webkit-font-smoothing: antialiased;
    font-family: 'Cabin', sans-serif;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}

html, body {
    touch-action: none
}

body {
    background: #EAEBEF;
    background-position-y: -100px;
    font-weight: 400;
}

@font-face {
    font-family: "GreycliffCF-ExtraBold";
    src: url("assets/font/GreycliffCF-Bold.ttf");
}

@font-face {
    font-family: "GreycliffCF-Heavy";
    src: url("assets/font/GreycliffCF-Heavy.ttf");
}

@font-face {
    font-family: "GreycliffCF-Medium";
    src: url("assets/font/GreycliffCF-Medium.ttf");
}

body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'GreycliffCF-Bold', 'GreycliffCF-Heavy', 'GreycliffCF-Medium', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
